:root {
  --pri-font:"Montserrat", sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --light-bg: #8e6ff3;
  --dark-bg: #675cf6;
  --dark-bg-main: #252237;
  --ctn-bg: #292B3C;
}

html {
  font-size: 100%;
  box-sizing: border-box;
  background-color: #252237;
}

*, *::before, *::after {
  box-sizing: inherit;
  list-style: none;
}

body {
  margin: 0;
  padding: 0;
  font-family: var(--pri-font);
}

.dn {
  display: none;
}

.vh {
  visibility: hidden;
}

h1, h2, h3 {
  margin-top: 0;
}

a, a:visited, a:active {
  text-decoration: none;
}

@media (max-width: 63.9375em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 64em) {
  .hide-for-desktop {
    display: none;
  }
}

@keyframes fade-in {
  from {
    visibility: hidden;
    opacity: 0;
  }
  1% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: visible;
    opacity: 1;
  }
}
.fade-in {
  animation: fade-in 0.3s ease-in-out forwards;
}

@keyframes fade-out {
  from {
    visibility: visible;
    opacity: 1;
  }
  99% {
    visibility: visible;
    opacity: 0;
  }
  to {
    visibility: hidden;
    opacity: 0;
  }
}
.fade-out {
  animation: fade-out 0.3s ease-in-out forwards;
}

.topCtn {
  background: linear-gradient(117deg, var(--light-bg) 0%, var(--dark-bg) 100%);
}

.coinSearchAddCtn, .coinCtn, .modal {
  background-color: var(--ctn-bg);
  border-radius: 8px;
  overflow-x: hidden;
}

.modal {
  width: 90%;
  max-width: 28.125rem;
  height: auto;
  overflow: hidden;
}
.modal div input {
  transition: all 0.2s ease-in-out;
}
.modal div input:focus {
  outline: 2px solid var(--dark-bg);
}
.modal div button {
  background: linear-gradient(117deg, var(--light-bg) 0%, var(--dark-bg) 100%);
  transition: all 0.2s ease-in-out;
}
.modal div button:hover {
  opacity: 0.8;
}

.coinSearchAddCtn form input {
  transition: all 0.2s ease-in-out;
}
.coinSearchAddCtn form input:focus {
  outline: 2px solid var(--dark-bg);
}
.coinSearchAddCtn form .btn_Ctn {
  position: relative !important;
}
.coinSearchAddCtn form .btn_Ctn img {
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.coinSearchAddCtn form button {
  background: linear-gradient(117deg, var(--light-bg) 0%, var(--dark-bg) 100%);
  transition: all 0.2s ease-in-out;
}
.coinSearchAddCtn form button:hover {
  opacity: 0.8;
}

